<template>
  <div class="mb-5">
    <nav class="level">
      <div class="level-item has-text-centered">
        <div>
          <a class="button is-danger" @click="makeActive">Add New Friend</a>
        </div>
      </div>
    </nav>

    <div class="modal is-active" v-if="isActive">
      <div class="modal-background"></div>
      <div class="modal-content">
        <!-- Any other Bulma elements you want -->
        <form @submit.prevent>
          <div class="field">
            <p class="control has-icons-left has-icons-right">
              <input
                class="input"
                type="text"
                placeholder="Name"
                v-model="enteredName"
              />
              <span class="icon is-small is-left">
                <i class="fas fa-user"></i>
              </span>
            </p>
          </div>
          <div class="field">
            <p class="control has-icons-left has-icons-right">
              <input
                class="input"
                type="email"
                placeholder="Email"
                v-model="enteredEmail"
              />
              <span class="icon is-small is-left">
                <i class="fas fa-envelope"></i>
              </span>
            </p>
          </div>
          <div class="field">
            <p class="control has-icons-left">
              <input
                class="input"
                type="tel"
                placeholder="Phone number"
                v-model="enteredPhoneNumber"
              />
              <span class="icon is-small is-left">
                <i class="fas fa-phone"></i>
              </span>
            </p>
          </div>
          <nav class="level">
            <div class="level-item has-text-centered">
              <div>
                <button
                  class="button is-danger"
                  @click="submitFriend"
                  type="submit"
                >
                  Add <i class="fas fas-plus"></i>
                </button>
              </div>
            </div>
          </nav>
        </form>
      </div>
      <button
        class="modal-close is-large"
        aria-label="close"
        @click="makeActive"
      ></button>
    </div>
  </div>
</template>


<script>
export default {
  methods: {
    makeActive() {
      this.isActive = !this.isActive;
    },
    submitFriend() {
      this.$emit(
        "submit-friend",
        this.enteredEmail,
        this.enteredPhoneNumber,
        this.enteredName
      );
      this.enteredEmail = "";
      this.enteredPhoneNumber = "";
      this.enteredName = "";
    },
  },
  data() {
    return {
      isActive: false,
      enteredEmail: "",
      enteredPhoneNumber: "",
      enteredName: "",
    };
  },
};
</script>


