<template>
  <div class="field has-addons">
    <p class="control">
      <input
        class="input is-info is-rounded"
        type="text"
        placeholder="Find a contact"
        @input="search"
        v-model="enteredValue"
      />
    </p>
  </div>
</template>

<script>
export default {
  methods: {
    search() {
      this.$emit("search", this.enteredValue);
    },
  },
  data() {
    return {
      enteredValue: "",
    };
  },
};
</script>

