<template>
  <div>
    <section class="hero has-background-primary-dark mb-3">
      <div class="hero-body">
        <nav class="level">
          <div class="level-item has-text-centered">
            <div>
              <p class="title is-1 has-text-white">Contacts</p>
            </div>
          </div>
          <div class="level-item has-text-centered">
            <div>
              <p class="title is-5">You Have {{ friends.length }} contact(s)</p>
            </div>
          </div>
          <div class="level-item has-text-centered">
            <div>
              <search-friend @search="searchFriends"></search-friend>
            </div>
          </div>
        </nav>
      </div>
    </section>
    <new-friend @submit-friend="addFriend"></new-friend>
    <div class="container is-fluid columns is-multiline" v-if="isMatched">
      <friend-contact
        v-for="friend in friends"
        :key="friend.id"
        :name="friend.name"
        :email-address="friend.email"
        :phone-number="friend.phone"
      ></friend-contact>
    </div>
    <div class="notification is-warning" v-else>
      There aren't any matching contacts!
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      friends: [],
      isMatched: true,
      // components: [NewFriendVue, FriendContactVue],
    };
  },
  methods: {
    addFriend(email, phoneNumber, name) {
      const newFriend = {
        id: name.split(" ")[0],
        name: name,
        email: email,
        phone: phoneNumber,
      };

      this.friends.push(newFriend);
      window.localStorage.setItem("friends", JSON.stringify(this.friends));
    },
    searchFriends(value) {
      // const MatchedFriends = this.friends.filter((friend) =>
      //   friend.name.toLowerCase().includes(value.toLowerCase())
      // );
      // console.log(value, MatchedFriends.name);
      const friendList = JSON.parse(window.localStorage.getItem("friends"));
      const matchedFriends = [];
      for (var i = 0; i < friendList.length; i++) {
        if (friendList[i].name.toLowerCase().includes(value.toLowerCase())) {
          matchedFriends.push(friendList[i]);
        }
      }
      if (matchedFriends.length === 0) {
        this.isMatched = false;
      } else {
        this.isMatched = true;
        this.friends = matchedFriends;
      }
    },
  },
  mounted() {
    const friendData = JSON.parse(window.localStorage.getItem("friends"));
    if (friendData) {
      this.friends = friendData;
    } else {
      this.friends = [];
    }
  },
};
</script>

